.slideshow__container {
  width: 100%;
  height: 500px;
  position: relative;
  margin: auto;
  overflow: hidden; }

.mySlides {
  display: none;
  position: relative;
  -o-object-fit: fill;
     object-fit: fill; }
  .mySlides img {
    position: absolute;
    height: 500px;
    width: 100%;
    -o-object-fit: fill;
       object-fit: fill; }

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 45%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 30px;
  -webkit-transition: 0.6s ease;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.next {
  right: 0;
  border-radius: 3px 0 0 3px; }

.prev:hover, .next:hover {
  background-color: rgba(0, 0, 0, 0.8); }

.text__block {
  position: absolute;
  top: 350px;
  left: 20px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding-left: 20px;
  padding-right: 20px; }
  .text__block h3 {
    padding: 5px 10px;
    margin: 0; }
  .text__block h5 {
    margin: 0;
    padding: 5px 10px; }

.show__curentSlide {
  width: 100%;
  position: absolute;
  text-align: center;
  bottom: 10px; }

.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  -webkit-transition: background-color 0.6s ease;
  transition: background-color 0.6s ease; }

.active, .dot:hover {
  background-color: #717171; }

@-webkit-keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }

@keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }

@-webkit-keyframes zooming {
  0% {
    height: 500px; }
  50% {
    height: 560px;
    width: 110%;
    margin-left: -5%;
    margin-top: -30px; }
  100% {
    height: 500px; } }

@keyframes zooming {
  0% {
    height: 500px; }
  50% {
    height: 560px;
    width: 110%;
    margin-left: -5%;
    margin-top: -30px; }
  100% {
    height: 500px; } }

@-webkit-keyframes textBoxEff {
  from {
    left: -250px; }
  to {
    left: 20px; } }

@keyframes textBoxEff {
  from {
    left: -250px; }
  to {
    left: 20px; } }

.fade {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s; }
  .fade img {
    -webkit-animation-name: zooming;
            animation-name: zooming;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear; }
  .fade .text__block {
    -webkit-animation-name: textBoxEff;
            animation-name: textBoxEff;
    -webkit-animation-duration: 1s;
            animation-duration: 1s; }
